import {
    login,
    register,
    logout,
    getInfo,
    getStudents,
    personalChange,
    createVote,
    voteHistory,
    attendVote,
    detail,
    submitVote,
    feedback,
    watchFeedback
} from '@/api/user'
import {
    getToken,
    setToken,
    removeToken
} from '@/utils/auth'
import router, {
    resetRouter
} from '@/router'

const state = {
    token: getToken(),
    id: '',
    name: '',
    birthday: '',
    phone: '',
    roles: [],
    students: [],
    choice: []
}

const mutations = {
    SET_TOKEN: (state, token) => {
        state.token = token
    },
    SET_PHONE: (state, phone) => {
        state.phone = phone
    },
    SET_ID: (state, id) => {
        state.id = id
    },
    SET_NAME: (state, name) => {
        state.name = name
    },
    SET_BIRTHDAY: (state, birthday) => {
        state.birthday = birthday
    },
    SET_ROLES: (state, roles) => {
        state.roles = roles
    },
    SET_STUDENTS: (state, students) => {
        state.students = students
    },
    RESET_CHOICE: (state) => {
        state.choice = []
    }
    // SET_TIPS: (state, type, message) => {
    //     state.tipsType = type
    //     state.tips = message
    // }
}

const actions = {
    // user login
    login({
        commit
    }, userInfo) {
        const {
            username,
            password
        } = userInfo
        return new Promise((resolve, reject) => {
            login({
                username: username.trim(),
                password: password
            }).then(response => {
                const {
                    data
                } = response
                // console.log(data)
                commit('SET_TOKEN', data.token)
                setToken(data.token)
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    register({
        commit
    }, userInfo) {
        const {
            username,
            name,
            password,
            birthday,
            phone,
            role
        } = userInfo
        return new Promise((resolve, reject) => {
            register({
                username: username.trim(),
                name: name.trim(),
                password: password,
                birthday: birthday,
                phone: phone,
                role: role
            }).then(response => {
                const {
                    data
                } = response
                // console.log(data)
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },



    // feedback
    feedback({
        state
    }, userInfo) {
        const {
            theme,
            content
        } = userInfo
        return new Promise((resolve, reject) => {
            feedback({
                theme: theme.trim(),
                content: content.trim(),
                token: state.token
            }).then(response => {
                const {
                    data
                } = response
                // console.log(data)
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },

    // get students
    watchFeedback({
        state
    }) {
        return new Promise((resolve, reject) => {

            watchFeedback({
                token: state.token
            }).then(response => {
                const {
                    data
                } = response

                if (!data) {
                    reject('认证失败，请重新登录')
                }

                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    personalChange({
        commit,
        state
    }, userInfo) {
        const {
            password,
            birthday,
            phone
        } = userInfo
        return new Promise((resolve, reject) => {
            personalChange({
                password: password,
                birthday: birthday,
                phone: phone,
                token: state.token
            }).then(response => {
                const {
                    data
                } = response
                // console.log(data)
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },



    // get user info
    getInfo({
        commit,
        state
    }) {
        return new Promise((resolve, reject) => {

            getInfo({
                token: state.token
            }).then(response => {
                const {
                    data
                } = response


                if (!data) {
                    reject('认证失败，请重新登录')
                }

                const {
                    roles,
                    name,
                    birthday,
                    phone,
                    id
                } = data
                // roles must be a non-empty array
                if (!roles || roles.length <= 0) {
                    reject('getInfo: roles must be a non-null array!')
                }
                commit('SET_ROLES', roles)
                commit('SET_NAME', name)
                commit('SET_BIRTHDAY', birthday)
                commit('SET_PHONE', phone)
                commit('SET_ID', id)

                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

    // get students
    getStudents({
        commit
    }) {
        return new Promise((resolve, reject) => {

            getStudents().then(response => {
                const {
                    data
                } = response

                if (!data) {
                    reject('认证失败，请重新登录')
                }

                const {
                    person
                } = data


                commit('SET_STUDENTS', person)
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

    // get voteHistory
    voteHistory({
        state
    }) {
        return new Promise((resolve, reject) => {

            voteHistory({
                token: state.token
            }).then(response => {
                const {
                    data
                } = response
                // console.log(data);


                if (!data) {
                    reject('认证失败，请重新登录')
                }


                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },


    // get attend Vote list
    attendVote({
        state
    }) {
        return new Promise((resolve, reject) => {

            attendVote({
                token: state.token
            }).then(response => {
                const {
                    data
                } = response
                // console.log(data);


                if (!data) {
                    reject('认证失败，请重新登录')
                }


                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

    // get vote detail
    detail({
        state
    }, id) {
        return new Promise((resolve, reject) => {
            detail({
                token: state.token,

            }, id).then(response => {
                const {
                    data
                } = response
                // console.log(data);


                if (!data) {
                    reject('认证失败，请重新登录')
                }


                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },



    //投票
    submitVote({
        state,
        commit
    }, id) {
        return new Promise((resolve, reject) => {
            console.log(state.choice)
            submitVote({
                id,
                choice: state.choice,
                token: state.token
            }).then(response => {

                commit('RESET_CHOICE')
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },




    // user logout
    logout({
        commit,
        state
    }) {
        return new Promise((resolve, reject) => {
            logout(state.token).then(() => {
                commit('SET_TOKEN', '')
                commit('SET_ROLES', [])
                removeToken()
                resetRouter()
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },

    createVote({
        commit,
        state
    }, voteInfo) {
        const {
            theme,
            startDate1,
            startDate2,
            endDate1,
            endDate2,
            selectMode,
            anonymous,
            filter,
            selectedAcademy,
            selectedGrade,
            selectedPerson,
            content,
            image,
            multipleSelected,
            singleSelected
        } = voteInfo
        return new Promise((resolve, reject) => {
            createVote({
                theme: theme,
                startDate1: startDate1,
                startDate2: startDate2,
                endDate1: endDate1,
                endDate2: endDate2,
                selectMode: selectMode,
                anonymous: anonymous,
                filter: filter,
                selectedAcademy: selectedAcademy,
                selectedGrade: selectedGrade,
                selectedPerson: selectedPerson,
                content: content,
                image: image,
                multipleSelected: multipleSelected,
                singleSelected: singleSelected,
                token: state.token

            }).then(response => {
                const {
                    data
                } = response
                // console.log(data)
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },

    // remove token
    resetToken({
        commit
    }) {
        return new Promise(resolve => {
            commit('SET_TOKEN', '')
            commit('SET_ROLES', [])
            removeToken()
            resolve()
        })
    },


    // Dynamically modify permissions
    // changeRoles({
    //     commit,
    //     dispatch
    // }, role) {
    //     return new Promise(async resolve => {
    //         const token = role + '-token'

    //         commit('SET_TOKEN', token)
    //         setToken(token)

    //         const {
    //             roles
    //         } = await dispatch('getInfo')

    //         resetRouter()

    //         // generate accessible routes map based on roles
    //         const accessRoutes = await dispatch('permission/generateRoutes', roles, {
    //             root: true
    //         })

    //         // dynamically add accessible routes
    //         router.addRoutes(accessRoutes)

    //         resolve()
    //     })
    // }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}